<script>
import MainLayout from '../../At/Layouts/MainLayout'
import ArticleBoxMini from '../../At/Components/BlogPage/ArticleBoxMini'

import * as Request from '../../Js/requests'
import * as Main from '../../Js/main'
import {marked} from 'marked'
import { Printd } from 'printd'
export default {
	name: 'BlogDetail',

    components: {MainLayout,ArticleBoxMini,},
	methods: {
	
		getArticle: function() { 		
	let config = {

		slug: {

			$contains: this.slug

		}

	}

	

	Request.getLocalTableArgs('articles', config)

	.then((res) => {

		this.article = res.data.data[0].attributes

		this.articleMeta = {'id': res.data.data[0].id}

		

		document.title = 'Afrotiative | ' + this.article.title

	

		document.getElementById('textContent').innerHTML 

			= `<div class="main">${marked.parse(this.article.content)}</div>`;

			

		this.getInstructor()

		this.getArticles()

	})

}
,
		getArticles: function() {			
	let config = {

		pagination: {

			limit: 5

		},

		sort: ['id:desc'],

		filters: {

			id: {

				$ne: 	this.articleMeta.id

			}

		}

	}

	

	Request.getLocalTableArgs('articles', config)

	.then((res) => {

		this.articles = res.data.data

	}) 

}
,
		getInstructor: function() { 		
	if(this.article.redactor){	

		let config = {	

		  filters: {

		    id: {

		      $eq: this.article.redactor.data.id,

		    },

		  },

		}	

			

		Request.getTableArgs('users', config)

		.then((res) => {

			this.redactor = res.data[0]

		})

	}

}
,
	},
	mounted(){
			
	this.getArticle()

	},
	computed: {
	
		primaryColor: function() { 		
	return this.$store.state.primaryColor

}
,
		selectedLang: function() { 		
	return this.$store.state.selectedLang

}
,
		title: function() { 		
	return this.article.title

}
,
		description: function() { 		
	return this.article.description

}
,
		redactorName: function() { 		
	return this.article.redactor.username

}
,
		redactorImage: function() { 		
	let item 

	if(this.redactor){	

		item = this.redactor.image;

	}

	return Main.getImgUrl(item, 'small');

}
,
		date: function() { 		
	return this.article.published_at

}
,
		image: function() { 		
	let item 

	if(this.article.image){	

		item = this.article.image.data.attributes;

	}

	return Main.getImgUrl(item, 'large');

}
,
		tags: function() { 		
	return (this.article.tags?

		this.article.tags.data.map(t => t.attributes)

		: []

	)

}
,
		link: function() { 		
	return '/blog/' + this.slug

}
,
		redactorName: function() { 		
	if(this.redactor){

		return this.redactor.username

	}

	return ""

}
,
		redactorImage: function() { 		
	let item 

	if(this.redactor){	

		item = this.redactor.image;

	}

	return Main.getImgUrl(item, 'small');

}
,
		date: function() { 		
	return this.article.publishedAt

}
,
		media: function() { 		
	if(this.article.multimedia){

		return this.article.multimedia.data.map(media => {	

			return Main.getImgUrl(media.attributes, 'large');

		})

	}

	return []

}
,
		slug: function() { 		
	return this.$route.params.slug

}
,
	},
	watch: {
	
		selectedLang: function() { 		
	this.getArticles()

}
,
		slug: function(val) { 		
	location.reload();

}
,
	},
	data(){
	return {
		
		article:  ({})		,
		articleMeta:  ({})		,
		articles:  []		,
		redactor:  null		,
		}
	},
} 
</script>

